<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Main Projects</h4></li>
            <div v-for="field in fields" v-bind:key="field.field_id">
                <li class="collection-item">
                    <div class="chip">{{field.name}}</div>
                    <router-link class="secondary-content"
                        v-bind:to="{name: 'view-field', params: {field_id: field.field_id}}">
                        Details
                    </router-link>
                </li>
            </div>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            fields: []
        }
    },
    mounted() {
        db.collection('fields').get().then(querySnapshot =>  {
            querySnapshot.forEach(doc => {
                const data = {
                    'field_id': doc.data().field_id,
                    'name': doc.data().name,
                    'projects': doc.data().fields
                }
                this.fields.push(data)
            });
        })
    }
}
</script>


