<template>
    <div id="edit-project">
        <h3>Edit Project</h3>
        <div class="row">
            <form @submit.prevent="updateProject" class="s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="project_id" required disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_desc" required disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_info" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="residual" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="date" v-model="to_start" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="date" v-model="to_end" required>
                    </div>
                </div>
                
                
                
                <!-- First section -->
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="section_name" required disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="contract_value" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="approved_30_6_20" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="approved_20_21" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="total_approved_2021" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="company_name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="outgoing" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="execution_rate" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="company_name_1" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="outgoing_1" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="execution_rate_1" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="contract_value_1" placeholder="Optional">
                    </div>
                </div>



                <!-- Second section -->
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="section_name_1" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="first_approved_30_6_20" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="first_approved_20_21" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="first_total_approved_2021" placeholder="Optional">
                    </div>
                </div>
                 <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="company_name_2" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="outgoing_2" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="execution_rate_2" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="company_name_3" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="outgoing_3" placeholder="Optional">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="execution_rate_3" placeholder="Optional">
                    </div>
                </div>

                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            residual: null,
            to_start: null,
            to_end: null,
            section_name: null,
            section_name_1: null,
            section_name_2: null,
            section_name_3: null,
            approved_30_6_20: null,
            first_approved_30_6_20: null,
            second_approved_30_6_20: null,
            third_approved_30_6_20: null,
            approved_20_21: null,
            first_approved_20_21: null,
            second_approved_20_21: null,
            third_approved_20_21: null,
            total_approved_2021: null,
            first_total_approved_2021: null,
            second_total_approved_2021: null,
            third_total_approved_2021: null,
            company_name: null,
            company_name_1: null,
            company_name_2: null,
            company_name_3: null,
            outgoing: null,
            outgoing_1: null,
            outgoing_2: null,
            outgoing_3: null,
            execution_rate: null,
            execution_rate_1: null,
            execution_rate_2: null,
            execution_rate_3: null,
            contract_value: null,
            contract_value_1: null,
            contract_value_2: null,
            contract_value_3: null,
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('projects').where('project_id', '==', to.params.project_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.project_id = doc.data().project_id
                    vm.project_desc = doc.data().project_desc
                    vm.project_info = doc.data().project_info
                    vm.residual = doc.data().residual
                    vm.to_start = doc.data().to_start
                    vm.to_end = doc.data().to_end
                    vm.section_name = doc.data().sections[0].section_name
                    vm.contract_value = doc.data().sections[0].contract_value
                    vm.approved_30_6_20 = doc.data().sections[0].approved_30_6_20
                    vm.approved_20_21 = doc.data().sections[0].approved_20_21
                    vm.total_approved_2021 = doc.data().sections[0].total_approved_2021

                    if (doc.data().sections[1]) {
                        vm.section_name_1 = doc.data().sections[1].section_name
                        vm.contract_value_1 = doc.data().sections[1].contract_value
                        vm.first_approved_30_6_20 = doc.data().sections[1].approved_30_6_20
                        vm.first_approved_20_21 = doc.data().sections[1].approved_20_21
                        vm.first_total_approved_2021 = doc.data().sections[1].total_approved_2021
                    }
                    
                    vm.company_name = doc.data().sections[0].companies[0].company_name
                    vm.outgoing = doc.data().sections[0].companies[0].outgoing
                    vm.execution_rate = doc.data().sections[0].companies[0].execution_rate
                    if (doc.data().sections[0].companies[1]) {
                        vm.company_name_1 = doc.data().sections[0].companies[1].company_name
                        vm.outgoing_1 = doc.data().sections[0].companies[1].outgoing
                        vm.execution_rate_1 = doc.data().sections[0].companies[1].execution_rate
                    }
                    if (doc.data().sections[1].companies[0]) {
                        vm.company_name_2 = doc.data().sections[1].companies[0].company_name
                        vm.outgoing_2 = doc.data().sections[1].companies[0].outgoing
                        vm.execution_rate_2 = doc.data().sections[1].companies[0].execution_rate
                    }
                    if (doc.data().sections[1].companies[1]) {
                        vm.company_name_3 = doc.data().sections[1].companies[1].company_name
                        vm.outgoing_3 = doc.data().sections[1].companies[1].outgoing
                        vm.execution_rate_3 = doc.data().sections[1].companies[1].execution_rate                        
                    }
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.project_id = doc.data().project_id
                        this.project_desc = doc.data().project_desc
                        this.project_info = doc.data().project_info
                        this.residual = doc.data().residual
                        this.to_start = doc.data().to_start
                        this.to_end = doc.data().to_end
                        this.section_name = doc.data().sections[0].section_name
                        this.contract_value = doc.data().sections[0].contract_value
                        this.approved_30_6_20 = doc.data().sections[0].approved_30_6_20
                        this.approved_20_21 = doc.data().sections[0].approved_20_21
                        this.total_approved_2021 = doc.data().sections[0].total_approved_2021
                        this.company_name = doc.data().sections[0].companies[0].company_name
                        this.outgoing = doc.data().sections[0].companies[0].outgoing
                        this.execution_rate = doc.data().sections[0].companies[0].execution_rate

                        if (doc.data().sections[1]) {
                            this.section_name_1 = doc.data().sections[1].section_name
                            this.contract_value_1 = doc.data().sections[1].contract_value
                            this.first_approved_30_6_20 = doc.data().sections[1].approved_30_6_20
                            this.first_approved_20_21 = doc.data().sections[1].approved_20_21
                            this.first_total_approved_2021 = doc.data().sections[1].total_approved_2021
                        }
                        
                        this.company_name = doc.data().sections[0].companies[0].company_name
                        this.outgoing = doc.data().sections[0].companies[0].outgoing
                        this.execution_rate = doc.data().sections[0].companies[0].execution_rate

                        if (doc.data().secttions[0].companies[1]) {
                            this.company_name_1 = doc.data().sections[0].companies[1].company_name
                            this.outgoing_1 = doc.data().sections[0].companies[1].outgoing
                            this.execution_rate_1 = doc.data().sections[0].companies[1].execution_rate                            
                        }
                        if (doc.data().secttions[1].companies[0]) {
                            this.company_name_2 = doc.data().sections[1].companies[0].company_name
                            this.outgoing_2 = doc.data().sections[1].companies[0].outgoing
                            this.execution_rate_2 = doc.data().sections[1].companies[0].execution_rate
                        }
                        if (doc.data().secttions[1].companies[1]) {
                            this.company_name_3 = doc.data().sections[1].companies[1].company_name
                            this.outgoing_3 = doc.data().sections[1].companies[1].outgoing
                            this.execution_rate_3 = doc.data().sections[1].companies[1].execution_rate
                        }
                    })
            })
        },
        updateProject() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            project_id: this.project_id,
                            project_desc: this.project_desc,
                            project_info: this.project_info,
                            residual: this.residual,
                            to_start: this.to_start,
                            to_end: this.to_end,
                            sections: [
                                {
                                    section_name: this.section_name,
                                    approved_30_6_20: this.approved_30_6_20,
                                    approved_20_21: this.approved_20_21,
                                    total_approved_2021: this.total_approved_2021,
                                    contract_value: this.contract_value,
                                    companies: [ 
                                        {
                                            company_name: this.company_name,
                                            outgoing: this.outgoing,
                                            execution_rate: this.execution_rate,
                                        },
                                        {
                                            company_name: this.company_name_1,
                                            outgoing: this.outgoing_1,
                                            execution_rate: this.execution_rate_1,
                                        },
                                    ]
                                },
                                {
                                    section_name: this.section_name_1,
                                    approved_30_6_20: this.first_approved_30_6_20,
                                    approved_20_21: this.first_approved_20_21,
                                    total_approved_2021: this.first_total_approved_2021,
                                    contract_value: this.contract_value_1,
                                    companies: [ 
                                        {
                                            company_name: this.company_name_2,
                                            outgoing: this.outgoing_2,
                                            execution_rate: this.execution_rate_2,
                                        },
                                        {
                                            company_name: this.company_name_3,
                                            outgoing: this.outgoing_3,
                                            execution_rate: this.execution_rate_3,
                                        },
                                    ]
                                },
                            ],
                        })
                        .then(() => {
                            this.$router.push({name: 'view-project', params:{project_id: this.project_id}})
                        })
                    })
            })
        }
    }
}
</script>


