<template>
    <div>
        <h4 class="center">Main Project Number: {{this.$route.params.field_id}}</h4>
        <h4 class="center">Main Project Name: {{this.field.name}}</h4>
        <ul class="collection with-header">
            <li class="collection-header"><h5>Projects acquainted with this main project:</h5></li>
            <div v-for="project in field.projects" v-bind:key="project.project_id">
                <li class="collection-item">
                    <div class="chip">{{project.project_id}}: {{project.project_desc}}</div>
                    <router-link class="secondary-content"
                        :to="{name: 'view-project', params: {project_id: project.project_id}}">
                        Details
                    </router-link>
                </li>
            </div>
        </ul>
        <router-link to="/" class="btn grey right">Back</router-link>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-field',
    data() {
        return {
            field: {}
        }
    },
    mounted() {
        db.collection('fields').where('field_id', '==', this.$route.params.field_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.field = doc.data()
                })
            })
    }
}
</script>


