<template>
    <div id="view-project">
        <div id="reactButtons">
            <router-link v-bind:to="{name: 'view-field', params: {field_id: this.$route.params.field_id}}" class="btn grey right">Back</router-link>
            <router-link to="/" id="mainProjectsMenu" class="btn grey">Main Projects Menu</router-link>
            <button @click="deleteProject" class="btn red">Delete</button>
        </div>
        <ul class="collection with-header">
            <li v-if="project.project_id" class="collection-header"><h4>رقم المشروع: {{project.project_id}}</h4></li>
            <li v-if="project.project_desc" class="collection-header"><h4>المشروع: {{project.project_desc}}</h4></li>
            <li v-if="project.project_info" class="collection-header"><h4>بيانات المشروع: {{project.project_info}}</h4></li>
            <li v-if="project.residual" class="collection-header"><h4>المتبقي: {{project.residual}}</h4></li>
            <li v-if="project.to_start" class="collection-header"><h4>تاريخ البدء: {{project.to_start}}</h4></li>
            <li v-if="project.to_end" class="collection-header"><h4>تاريخ النهو: {{project.to_end}}</h4></li>
            <li v-if="project.project_id" class="collection-header"><h3>القطاعات</h3></li>
            <div v-for="section in project.sections" v-bind:key="section.section_name">
                <li v-if="section.section_id" class="collection-header"><h3>القطاع رقم {{ section.section_id }}</h3></li>
                <li v-if="section.section_name" class="collection-header"><h4>الوصف: {{section.section_name}}</h4></li>
                <li v-if="section.contract_value" class="collection-header"><h4>قيمة التعاقد: {{section.contract_value}}</h4></li>
                <li v-if="section.approved_30_6_20" class="collection-header"><h4>المعتمد حتى 30/6/2020: {{section.approved_30_6_20}}</h4></li>
                <li v-if="section.approved_20_21" class="collection-header"><h4>المعتمد 2020/2021: {{section.approved_20_21}}</h4></li>
                <li v-if="section.total_approved_2021" class="collection-header"><h4>المعتمد حتى 30/6/2021: {{section.total_approved_2021}}</h4></li>
                <li v-if="section.companies.company_id" class="collection-header"><h3>الشركات</h3></li>
                <div v-for="company in section.companies" v-bind:key="company.company_name">
                    <li v-if="company.company_id" class="collection-header"><h3>الشركة رقم {{ company.company_id }}</h3></li>
                    <li v-if="company.company_id" class="collection-header"><h4>الشركة المنفذة: {{company.company_name}}</h4></li>
                    <li v-if="company.outgoing" class="collection-header"><h4>المنصرف: {{company.outgoing}}</h4></li>
                    <li v-if="company.execution_rate" class="collection-header"><h4>نسبة التنفيذ الفعلي: {{company.execution_rate}}</h4></li>
                </div>
            </div>
        </ul>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-project', params: {project_id: this.$route.params.project_id}}" class="btn-floating btn-large red">
                تعديل
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-project',
    data() {
        return {
            field: {},
            project: {},
            projects: [],
        }
    },
    mounted() {
        db.collection('fields').where('field_id', '==', this.$route.params.field_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.field = doc.data()
                    this.projects = doc.data().projects
                })
                this.projects.forEach(doc => {
                    if (doc.project_id == this.$route.params.project_id) {
                        this.project = doc
                    }
                })
            })
    },
    methods: {
        deleteProject() {
            if(confirm('Are you sure?')) {
                db.collection('fields').where('field_id', '==', this.$route.params.field_id).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                })
            }
        }
    }
}
</script>

<style scoped>
#reactButtons {
    padding-top: 1%;
}
</style>


